<template>
    <div class="settings">
        <h1>选项</h1>
        <p>如何称呼您？</p>
        <input type="text" v-model="configName" />
        <p>hitokoto</p>
        <label><input type="checkbox" checked="checked" />开启</label>
        <button v-on:click="saveConfig">保存配置</button>
    </div>
</template>

<script>

export default {
    name: 'settings',
    data(){
        return {
            configName: "",
            config: {
                name: ""
            }
        }
    },

    created: function(){
        var that = this;
        chrome.storage.sync.get(['config'],function(data){
            if(data.config){
                that.config = data.config;
                console.log("storage-get:config");
                console.log(data.config);
                that.configName = that.config.name;
            }
            else{
                chrome.storage.sync.set({"config":that.config},function(){
                    console.log("storage-set:config");
                })
            }
        });
    },

    methods: {
        saveConfig: function(){
            var that = this;
            that.config.name = that.configName;
            chrome.storage.sync.set({"config":that.config},function(){
                console.log("storage-set:config");
                console.log(that.config);
            });
        }
    }
}

</script>