import Vue from 'vue'
import VueRouter from 'vue-router'
import options from './options.vue'
import about from './about.vue'
import settings from './settings.vue'
import floder from './floder.vue'
import './iconfont.js'
Vue.use(VueRouter);
Vue.config.productionTip = false;

const routes = [
  {
    path: "/",
    component: about
  },
  {
    path: "/about",
    component: about
  },
  {
    path: "/settings",
    component: settings
  },
  {
    name: 'floder',
    path: "/floder/:id",
    component: floder
  }
];

const router = new VueRouter({
  routes
});

new Vue({
  router,
  render: h => h(options),
}).$mount('#options')
