<template>
    <div id="floder">

        <div v-if="mark.floder" class="floderInfo">
            <h1>文件夹：{{mark.floder[$route.params.id].name}}</h1>
            <div class="floderOptions">
                <button>
                    <svg class="icon"><use xlink:href="#icon-delete"></use></svg>删除
                </button>
            </div>
        </div>

        <ul v-if="mark.floder">
            <li v-for="list of mark.floder[$route.params.id].list">
                <div class="listInfo">
                    <div class="listBasic">
                        <div class="listBasic-1">{{list.title}}</div>
                        <div class="listBasic-2">{{list.url}}</div>
                    </div>
                    <div class="listImage"><div class="image" v-bind:style="{backgroundImage:'url('+list.src+')'}"></div><div class="listSrc">{{list.src}}</div></div>
                </div>
                <div class="listDelete">
                    <svg class="icon">
                        <use xlink:href="#icon-delete"></use>
                    </svg>
                </div>
            </li>
        </ul>


        <div class="addMark">
            <input type="text" placeholder="书签名" v-model="markTitle" />
            <input type="text" placeholder="书签地址" v-model="markUrl"/>
            <div class="inputImage"><div class="image" v-bind:style="{backgroundImage:'url('+markSrc+')'}"></div><input type="text" style="padding-left:30px;" placeholder="图标地址" v-model="markSrc"/></div>
            <button v-on:click="addMark">添加书签</button>
        </div>
        
    </div>
</template>

<script>
export default {
    name: 'floder',
    data(){
        return {
            markTitle: "",
            markUrl: "",
            markSrc: "",
            mark: ""
        }
    },

    created: function(){
        var that = this;
        chrome.storage.sync.get(['mark'],function(data){
            if(data.mark){
                that.mark = data.mark;
                console.log("storage-get:mark");
                console.log(that.mark);
            }
            else{
                window.history.back();
                return;
            }
        }); 
    },

    methods: {
        addMark: function(){
            var that = this
            console.log(that.$route.params.id);
            that.mark.floder[that.$route.params.id].list.push({
                title: that.markTitle,
                url: that.markUrl,
                src: that.markSrc
            });
            that.mark.floder[that.$route.params.id].num++;
            chrome.storage.sync.set({"mark": that.mark},function(){
                console.log("storage-set:mark-list");
            })
        }
    }
}
</script>

<style scoped>
    .floderInfo {
        width: 100%;
        position: relative;
        margin-bottom: 15px;
    }
    .floderInfo h1 {
        width: 100%;
        line-height: 60px;
        margin: 0;
        border-bottom: 1px solid #eee;
    }
    .floderInfo .floderOptions {
        position: absolute;
        top: 13px;
        right: 0;
    }
    .floderOptions button {
        line-height: 32px;
        border: 1px solid #d43f3a;
        border-radius: 3px;
        background-color: #d9534f;
        color: white;
        font-size: 14px;
    }

    ul {
        width: 700px;
    }
    ul li {
        width: 100%;
        margin-bottom: 15px;
        padding-bottom: 10px;
        border-bottom: 1px solid #eee;
    }
    .listInfo {
        display: inline-block;
        width: 600px;
        height: 52px;
    }
    .listBasic {
        width: 100%;
        height: 50%;
    }
    .listBasic div {
        display: inline-block;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        line-height: 26px;
        font-size: 14px;
    }
    .listBasic-1 {
        width: 40%;
    }
    .listBasic-2 {
        padding-left: 15px;
        width: 60%;
    }
    .listImage {
        width: 100%;
        height: 50%;
    }
    .listImage .image {
        display: inline-block;
        width: 26px;
        height: 26px;
        background-position: 50% 50%;
        background-size: cover;
    }
    .listImage .listSrc {
        width: calc(100% - 26px);
        padding-left: 15px;
        display: inline-block;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        line-height: 26px;
        font-size: 14px;  
    }
    .listDelete {
        display: inline-block;
        width: 100px;
        line-height: 52px;
    }

    .addMark {
        width: 700px;
    }
    .addMark input {
        display: block;
        width: 100%;
        font-size: 14px;
        line-height: 30px;
        border: 1px solid #ddd;
        border-radius: 3px;
    }
    .inputImage {
        width: 100%;
        position: relative;
    }
    .addMark .image {
        position: absolute;
        width: 24px;
        height: 24px;
        top: 0;
        left: 0;
    }
</style>
