<template>
  <div id="options">

    <ul id="optionsList">
      <router-link to="/about" id="a-about">TabTop</router-link>
      <p class="classify">设定</p>
      <li><router-link to="/settings">选项</router-link></li>
      <p class="classify">文件夹</p>
      <li v-for="(floder, i) of mark.floder">
        <router-link :to="{name: 'floder',params: {id: i}}">
          {{floder.name}}
        </router-link>
      </li>
      <li v-on:click="openAddFolder">添加</li>
    </ul>

    <div class="content">
			<router-view></router-view>
		</div>

    <transition name="addFolder">
      <div v-if="addFolderShow" id="addFolder">
        <div class="content">
          <p>新建文件夹</p>
          <hr />
          <label>文件夹名称</label>
          <input v-model="folderName" />
          <span>*名称为1-8个字符以内</span>
          <hr />
          <div>
            <button v-on:click="createFolder">创建</button>
            <button v-on:click="cancelAddFolder">取消</button>
          </div>
        </div>
      </div>
    </transition>

  </div>
</template>

<script>

export default {
  name: 'options',
  data(){
    return {
      addFolderShow: false,
      folderName: "",

      mark: {
        "num": 0,
        "floder": new Array()
      }
    }
  },

  created: function(){
    var that = this;
    chrome.storage.sync.get(['mark'],function(data){
      if(data.mark){
        that.mark = data.mark;
        console.log("storage-get:mark");
        console.log(data.mark);
      }
      else{
        chrome.storage.sync.set({"mark":that.mark},function(){
          console.log("storage-set:mark");
        })
      }
    });  
  },

  methods: {
    openAddFolder:function(){
      this.addFolderShow = true;
    },
		cancelAddFolder:function(){
      this.addFolderShow = false;
		},
		createFolder:function(){
			var that = this;
			var folder = {
				"name": that.folderName,
				"num": 0,
				"list":new Array()
			}
			that.mark.floder.push(folder);
			that.mark.num++;
			chrome.storage.sync.set({"mark":that.mark},function(){
				console.log("storage-set:mark"+that.mark);
			});
		}
  },
}
</script>

