	background-color: rgba(0,0,0,.2);
}
#addFolder>.content {
	position: absolute;
	top: 20%;
	left: calc(50% - 200px);
	width: 400px;
	padding: 5px;
	border: 1px solid #dddddd;
	border-radius: 3px;
	background-color: white;
}
