	display: block;
	width: 100%;
	padding: 5px;
	font-size: 18px;
	border: 1px solid #dddddd;
	border-radius: 3px;
}

#options>.content {
	display: inline-block;
	width: calc(100% - 300px);
  height: 100vh;
  padding: 15px;
	vertical-align: middle;
}
